'use client';

import { useRef, useEffect } from 'react';
import gsap from 'gsap';
import { useGSAP } from '@gsap/react';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

export default function Hero() {
    const containerRef = useRef<HTMLDivElement>(null);
    const marqueeRef = useRef<HTMLDivElement>(null);
    const line1Ref = useRef<HTMLDivElement>(null);
    const line2Ref = useRef<HTMLDivElement>(null);

    useGSAP(() => {
        gsap.registerPlugin(ScrollTrigger);

        // 1. Giant Background Loop
        gsap.to(marqueeRef.current, {
            xPercent: -50,
            duration: 40,
            ease: "none",
            repeat: -1,
            force3D: true,
        });

        // 2. Theme Transition Engine (White -> Black)
        ScrollTrigger.create({
            trigger: containerRef.current,
            start: "bottom 90%",
            onEnter: () => {
                gsap.to("body", { backgroundColor: "#0a0a0c", color: "#ffffff", duration: 1.2, ease: "power2.inOut" });
                gsap.to(".nav-wrap", { opacity: 1, duration: 0.5 });
            },
            onLeaveBack: () => {
                gsap.to("body", { backgroundColor: "#ffffff", color: "#000000", duration: 1.2, ease: "power2.inOut" });
            }
        });

        // 3. Foreground Reveal (Mask Wipe + Letter Stagger + Upward Motion)
        const tl = gsap.timeline({ delay: 0.5 });
        const splitLine1 = line1Ref.current?.querySelectorAll('.char');
        const splitLine2 = line2Ref.current?.querySelectorAll('.char');

        tl.fromTo(splitLine1 || [],
            { clipPath: 'inset(100% 0 0 0)', y: 80, opacity: 0 },
            {
                clipPath: 'inset(0% 0 0 0)',
                y: 0,
                opacity: 1,
                stagger: 0.06,
                duration: 1.5,
                ease: "expo.out"
            }
        ).fromTo(splitLine2 || [],
            { clipPath: 'inset(100% 0 0 0)', y: 80, opacity: 0 },
            {
                clipPath: 'inset(0% 0 0 0)',
                y: 0,
                opacity: 1,
                stagger: 0.06,
                duration: 1.5,
                ease: "expo.out"
            },
            "-=1.2"
        ).fromTo(".hero-subtext",
            { y: 30, opacity: 0 },
            { y: 0, opacity: 1, duration: 1.2, ease: "power3.out" },
            "-=1.0"
        ).fromTo(".hero-ctas",
            { y: 30, opacity: 0, scale: 0.9 },
            { y: 0, opacity: 1, scale: 1, duration: 1, ease: "back.out(1.7)" },
            "-=0.8"
        );
    }, { scope: containerRef });

    const renderCharacters = (text: string) => {
        return text.split('').map((char, i) => (
            <span key={i} className="char inline-block will-change-transform">
                {char === ' ' ? '\u00A0' : char}
            </span>
        ));
    };

    return (
        <section
            ref={containerRef}
            className="hero-section relative w-full h-screen overflow-hidden flex items-center justify-center bg-white text-black"
        >
            {/* Layer 1: Background Marquee (Giant Word) */}
            <div className="absolute inset-0 flex items-center justify-center z-0 pointer-events-none overflow-hidden select-none">
                <div
                    ref={marqueeRef}
                    className="flex whitespace-nowrap will-change-transform opacity-[0.06]"
                    style={{ transform: 'translate3d(0,0,0)' }}
                >
                    <span className="font-display font-black uppercase text-black line-through decoration-1 px-[5vw]" style={{ fontSize: '25vw' }}>FUSIONKODE</span>
                    <span className="font-display font-black uppercase text-black line-through decoration-1 px-[5vw]" style={{ fontSize: '25vw' }}>FUSIONKODE</span>
                </div>
            </div>

            {/* Layer 2: Foreground Content */}
            <div className="relative z-10 w-full max-w-[1440px] px-8 md:px-16 flex flex-col items-center text-center">
                <div className="flex flex-col gap-6 md:gap-8">
                    <div ref={line1Ref} className="overflow-hidden">
                        <h2 className="font-mono uppercase tracking-[0.4em] text-black/40 mb-2" style={{ fontSize: 'var(--font-size-small)' }}>
                            {renderCharacters('Architecting Digital Intelligence')}
                        </h2>
                    </div>
                    <div ref={line2Ref} className="overflow-hidden">
                        <h1 className="font-display font-black uppercase text-black tracking-tighter leading-[0.8] mb-4" style={{ fontSize: 'var(--font-size-hero)' }}>
                            {renderCharacters('FUSIONKODE')}
                        </h1>
                    </div>
                    <div className="hero-subtext opacity-0 max-w-2xl mx-auto">
                        <p className="font-sans text-black/60 md:text-xl font-light leading-relaxed">
                            Engineering high-performance automation systems and premium digital experiences for the next generation of business.
                        </p>
                    </div>
                    <div className="hero-ctas opacity-0 mt-12 flex flex-col md:flex-row justify-center gap-6">
                        <a href="#contact" className="px-10 py-5 rounded-xl bg-black text-white font-mono text-xs uppercase tracking-widest hover:scale-105 active:scale-95 transition-all shadow-2xl">
                            Deploy System ‚Üí
                        </a>
                        <a href="#work" className="px-10 py-5 rounded-xl border border-black/10 bg-black/5 text-black font-mono text-xs uppercase tracking-widest hover:bg-black/10 transition-all">
                            View Lab Logs
                        </a>
                    </div>
                </div>
            </div>

            <div className="absolute bottom-12 left-1/2 -translate-x-1/2 flex flex-col items-center gap-4 opacity-20 animate-bounce">
                <span className="font-mono text-[9px] uppercase tracking-[0.4em]">Scroll</span>
                <div className="w-[1px] h-12 bg-black" />
            </div>
        </section>
    );
}
