'use client';

import { useState, useEffect } from 'react';

const navItems = [
    { name: 'Services', href: '#services' },
    { name: 'Work', href: '#work' },
    { name: 'Philosophy', href: '#philosophy' },
    { name: 'Contact', href: '#contact' },
];

export default function FloatingNav() {
    return (
        <div className="nav-wrap fixed top-0 left-0 w-full z-[100] transition-all duration-700 opacity-100 pointer-events-auto">
            <nav className="w-full h-20 md:h-24 glass-border-bottom backdrop-blur-md flex items-center justify-center px-6 md:px-12">
                <div className="w-full max-w-[1440px] flex items-center justify-between">
                    <div className="flex items-center gap-2">
                        <div className="h-2 w-2 rounded-full bg-cyan shadow-[0_0_8px_rgba(0,242,255,0.4)]" />
                        <span className="font-display font-black uppercase tracking-tighter text-xl">FUSIONKODE</span>
                    </div>

                    <div className="flex items-center gap-10">
                        <div className="hidden md:flex items-center gap-8">
                            {['Services', 'Builds', 'Lab', 'Contact'].map((item) => (
                                <a
                                    key={item}
                                    href={`#${item.toLowerCase()}`}
                                    className="font-mono text-[10px] uppercase tracking-[0.3em] text-white/50 hover:text-cyan transition-colors"
                                >
                                    {item}
                                </a>
                            ))}
                        </div>

                        <a href="#contact" className="px-6 py-3 bg-white/5 border border-white/10 rounded-full font-mono text-[9px] uppercase tracking-widest text-white hover:bg-cyan hover:text-black transition-all">
                            Initialize
                        </a>
                    </div>
                </div>
            </nav>
        </div>
    );
}
