'use client';

import { useRef } from 'react';
import gsap from 'gsap';
import { useGSAP } from '@gsap/react';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

const services = [
    { title: "AI Agents Development", desc: "Custom autonomous agents (like Agent NEXA) built with RAG architecture for complex multi-step workflows.", icon: "ü§ñ" },
    { title: "AI Automation Agency", desc: "End-to-end business process automation, integrating disparate silos into a unified intelligence layer.", icon: "‚öôÔ∏è" },
    { title: "Web App Systems", desc: "High-performance Next.js architectures with cinematic motion UI and conversion-first engineering.", icon: "üöÄ" },
    { title: "Neural SEO Systems", desc: "Intelligent content velocity and topical authority engines powered by semantic intent mapping.", icon: "üß†" }
];

export default function Services() {
    const containerRef = useRef<HTMLDivElement>(null);

    useGSAP(() => {
        gsap.registerPlugin(ScrollTrigger);
        gsap.fromTo(".service-card",
            { y: 50, opacity: 0 },
            { y: 0, opacity: 1, duration: 1.2, ease: "power3.out", stagger: 0.2, scrollTrigger: { trigger: containerRef.current, start: "top 80%" } }
        );
    }, { scope: containerRef });

    return (
        <section ref={containerRef} id="services" className="relative w-full py-24 md:py-48 px-6 md:px-12 flex flex-col items-center overflow-hidden">
            {/* DEPTH GLOW */}
            <div className="absolute inset-0 depth-glow-cyan pointer-events-none opacity-10" />

            <div className="w-full max-w-[1320px] relative z-10">
                <div className="mb-24 flex flex-col gap-6 text-center md:text-left">
                    <span className="font-mono text-[12px] uppercase tracking-[0.5em] text-cyan/40">[ OUR CORE PROTOCOLS ]</span>
                    <h3 className="font-display font-black uppercase tracking-tighter text-white leading-none" style={{ fontSize: 'var(--font-size-h2)' }}>ENGINEERING <br /> SOLUTIONS.</h3>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                    {services.map((service, i) => (
                        <div key={i} className="service-card glass-panel rounded-2xl p-10 group hover:border-cyan/30 transition-all duration-700 hover:scale-[1.02]">
                            <div className="flex flex-col gap-8">
                                <span className="text-4xl filter grayscale group-hover:grayscale-0 transition-all duration-500">{service.icon}</span>
                                <div className="flex flex-col gap-4">
                                    <h4 className="font-display text-2xl md:text-3xl font-bold uppercase text-white group-hover:text-cyan transition-colors duration-500">{service.title}</h4>
                                    <p className="font-sans text-white/40 leading-relaxed text-lg font-light">{service.desc}</p>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </section>
    );
}
