'use client';

import { useRef } from 'react';
import gsap from 'gsap';
import { useGSAP } from '@gsap/react';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

const labExperiments = [
    { phase: "AUTO_1", status: "Live", title: "Autonomous Lead Flow", desc: "Self-optimizing lead qualification systems for high-volume B2B pipelines." },
    { phase: "AGENT_2", status: "Testing", title: "Neural Support Bot", desc: "Cross-platform customer service agent with memory and intent resolution." },
    { phase: "SEO_3", status: "Building", title: "Semantic Growth Engine", desc: "Real-time SEO audit and content generation systems for topical authority." },
    { phase: "WEB_4", status: "Live", title: "High-Velocity Web", desc: "Ultra-fast Next.js deployments with cinematic motion and performance scoring." }
];

export default function Timeline() {
    const containerRef = useRef<HTMLDivElement>(null);
    const lineRef = useRef<HTMLDivElement>(null);

    useGSAP(() => {
        gsap.registerPlugin(ScrollTrigger);
        gsap.to(lineRef.current, { scaleY: 1, ease: "none", scrollTrigger: { trigger: containerRef.current, start: "top 50%", end: "bottom 50%", scrub: true } });
        gsap.fromTo(".timeline-item", { x: -50, opacity: 0 }, { x: 0, opacity: 1, duration: 1.2, ease: "power3.out", stagger: 0.3, scrollTrigger: { trigger: containerRef.current, start: "top 80%" } });
    }, { scope: containerRef });

    return (
        <section ref={containerRef} id="lab" className="relative w-full py-24 md:py-48 bg-transparent px-6 md:px-12 flex flex-col items-center overflow-hidden">
            {/* DEPTH GLOW */}
            <div className="absolute inset-0 depth-glow-violet pointer-events-none opacity-20" />

            <div className="w-full max-w-[1320px] relative">
                <div className="mb-24 flex flex-col gap-6 text-center md:text-left relative z-10">
                    <span className="font-mono text-[12px] uppercase tracking-[0.5em] text-violet/40">[ FUSIONKODE BUILD LAB ]</span>
                    <h3 className="font-display font-black uppercase tracking-tighter text-white leading-none" style={{ fontSize: 'var(--font-size-h2)' }}>SYSTEMS <br /> UNDER PRODUCTION.</h3>
                </div>

                <div ref={lineRef} className="absolute left-[20px] md:left-[51px] top-0 w-[1px] h-full bg-white/5 origin-top scale-y-0" />

                <div className="flex flex-col gap-24 relative z-10">
                    {labExperiments.map((exp, i) => (
                        <div key={i} className="timeline-item flex gap-12 items-start group">
                            <div className="relative z-20 w-12 md:w-24 flex flex-col items-center">
                                <div className={`w-3 h-3 rounded-full ${exp.status === 'Live' ? 'bg-cyan shadow-[0_0_15px_rgba(0,242,255,0.8)]' : 'bg-white/20'} animate-pulse`} />
                                <span className="font-mono text-[10px] text-white/10 uppercase tracking-widest mt-8 [writing-mode:vertical-lr]">{exp.phase}</span>
                            </div>
                            <div className="flex-1 p-10 glass-panel rounded-2xl">
                                <div className="flex items-center justify-between mb-6">
                                    <span className={`px-3 py-1 rounded-full font-mono text-[8px] uppercase tracking-widest border border-white/10 ${exp.status === 'Live' ? 'text-cyan bg-cyan/5 border-cyan/20' : 'text-white/40 bg-white/5'}`}>
                                        Protocol Status: {exp.status}
                                    </span>
                                </div>
                                <h4 className="font-display text-2xl md:text-3xl font-black uppercase text-white group-hover:text-cyan transition-colors duration-500">{exp.title}</h4>
                                <p className="font-sans text-white/40 leading-relaxed mt-4 font-light text-lg">{exp.desc}</p>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </section>
    );
}
